.newai__whatai {
    display: flex;
    flex-direction: column;
    padding: 2rem;
    background: var(--color-footer);
    background:radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.newai__whatai-feature{
    display: flex;
}

.newai__whatai-feature .newai__features-container__feature{
    margin: 0;
}

.newai__whatai-feature .newai__features-container__feature-text{
    max-width: 700px;
}

.newai__whatai-heading{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0 2rem;
}

.newai__whatai-heading h1{
    font-family: var(--font-family);
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    max-width: 500px;
    color: transparent;
}

.newai__whatai-heading p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 17px;
    line-height: 30px;
    color: var(--color-subtext);
    cursor: pointer;
}

.newai__whatai-container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;
}

.newai__whatai-container .newai__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

.newai__whatai-container .newai__features-container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .newai__whatai-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .newai__whatai-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    .newai__whatai-feature .newai__features-container__feature {
        flex-direction: column;
    }

    .newai__whatai-feature .newai__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .newai__whatai-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    .newai__whatai-container .newai__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}